@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@500;700&display=swap);
html{font-size:100%}body{margin:0;text-align:center;font-family:'Open Sans', sans-serif}a:link,a:visited{text-decoration:none}ul{padding:0}nav ul li a,#home #home-content{color:white;-webkit-filter:drop-shadow(0 0 0.05rem black);filter:drop-shadow(0 0 0.05rem black)}nav{position:fixed;width:100%;opacity:0;transition:opacity 0.2s, background-color 0.2s;z-index:1}nav.show{opacity:1}nav.opaque{opacity:1;background-color:#161b22}nav ul{list-style:none;margin:0;text-align:center}nav ul li{display:inline-block;padding:1rem 0}nav ul li a{padding:1rem 1rem;transition:color 0.2s}nav ul li a.active{color:orange}nav ul li a:hover{cursor:pointer}#home{display:flex;flex-direction:column;justify-content:center;height:100vh;min-height:55vw}#home #rain-image,#home #rain-video{position:absolute;object-fit:cover;object-position:50% 0%;width:100%;height:100vh;min-height:inherit}#home #rain-image{z-index:-2}#home #rain-video{z-index:-1}#home>div{flex:1 1}#home #home-content{display:flex;flex-direction:column;justify-content:center;padding:5rem 0}#home #home-content #name,#home #home-content #roles{margin:0}#home #home-content #name{font-size:3rem;font-weight:700}#home #home-content #roles{margin-bottom:0.25rem;font-size:0.9rem;font-weight:500}#home #home-content #socials{font-size:3rem}#home #home-content #socials *{margin:0 0.5rem}#home #home-content #wip{font-size:1.25rem}#home #home-content #wip *{margin:0 0.25rem}#about,#resume{height:100vh}@media (min-width: 60vh){#home #rain-image,#home #rain-video{object-position:0% 100%}}

